nav.primary-navigation ul li {
	list-style: none;
	margin: 0 auto;
	display: inline-block;
    margin-top: 10px;
	padding: 0 24px;
	position: relative;
	text-decoration: none;
	text-align: center;
	font-family: arvo;
}

nav.primary-navigation li a {
	color: black;
}

nav.primary-navigation li a:hover {
	color: #3ca0e7;
}

nav.primary-navigation li:hover {
	cursor: pointer;
}

nav.primary-navigation ul li ul {
	visibility: hidden;
	opacity: 0;
	position: absolute;
	padding-left: 0;
	left: 0;
	display: none;
	background: white;
}

nav.primary-navigation ul li:hover > ul,
  nav.primary-navigation ul li ul:hover {
	visibility: visible;
	opacity: 1;
	display: block;
	min-width: 250px;
	text-align: left;
	padding-top: 20px;
	box-shadow: 0px 3px 5px -1px #ccc;
}

nav.primary-navigation ul li ul li {
	clear: both;
	width: 100%;
	text-align: left;
	margin-bottom: 20px;
	border-style: none;
}

nav.primary-navigation ul li ul li a:hover {
	padding-left: 10px;
	transition: all 0.3s ease;
}

a {
