	display: flex;
	flex-wrap: wrap;
	flex-direction: column;
	justify-content: center;
	-webkit-animation-name: left;
	        animation-name: left;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-fill-mode: both;
	        animation-fill-mode: both;
	-webkit-animation-delay: 1s;
	        animation-delay: 1s;
}

.right-login {
